// for all pages
import Vue from "vue";
import VueRouter from "vue-router";
import Tweets from "../views/Tweets.vue";
import TargetTweet from "../views/TargetTweet.vue";
import User from "../views/User.vue";
import Login from "../views/Login.vue";
import Admin from "../views/Admin.vue";
import Regist from "../views/Regist.vue";
import NotFound from "../views/NotFound.vue";
import store from "./../store";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "root",
    redirect: "/login", // 
  },
  {
    path: "/tweets",
    name: "tweets",
    component: Tweets, // 
  },
  {
    path: "/tweets/targettweet",
    name: "targettweet",
    component: TargetTweet, // 
  },
  {
    path: "/user",
    name: "user",
    component: User, // 
  },
  {
    path: "/login",
    name: "log-in",
    component: Login, // 
  },
  {
    path: "/admin",
    name: "admin",
    component: Admin, // 
  },
  {
    path: "/regist",
    name: "regist",
    component: Regist, // 
  },
  {
    path: "/tweets/user/userpost",
    name: "userpost",
    component: () => import("../components/UserPost.vue"), // 
  },
  {
    path: "/tweets/user/userreply",
    name: "userreply",
    component: () => import("../components/UserReply.vue"), // 
  },
  {
    path: "/tweets/user/userlikes",
    name: "userlikes",
    component: () => import("../components/UserLikes.vue"), // 
  },
  {
    path: "/tweets/user/setting",
    name: "setting",
    component: () => import("../components/Setting.vue"), // 
  },
  {
    path: "/tweets/user/followers",
    name: "followers",
    component: () => import("../views/Followers.vue"), // 
  },
  {
    path: "/tweets/user/following",
    name: "following",
    component: () => import("../views/Following.vue"), // 
  },
  {
    path: "/tweets/otheruser",
    name: "otheruser",
    component: () => import("../views/OtherUser.vue"), //  user profile
  },
  {
    path: "/admin/postslist",
    name: "postslist",
    component: () => import("../views/PostsList.vue"), // 
  },
  {
    path: "/admin/userslist",
    name: "userslist",
    component: () => import("../views/UsersList.vue"), // 
  },
  {
    path: "*",
    name: "not-found",
    component: NotFound, // 
  },
];

const router = new VueRouter({
  linkExactActiveClass: "active",
  routes,
});

router.beforeEach((to, from, next) => {
  //console.log('to', to)
  //console.log('from', from)
  //  dispatch  Vuex  actions
  store.dispatch("fetchCurrentUser");
  next();
});

export default router;
